import { IonicStorageModule } from '@ionic/storage';
import { IonicSelectableModule } from 'ionic-selectable';
import { Camera } from '@ionic-native/Camera/ngx';
import { NgModule } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";
import { RouteReuseStrategy } from "@angular/router";

import { IonicModule, IonicRouteStrategy } from "@ionic/angular";
import { SplashScreen } from "@ionic-native/splash-screen/ngx";
import { StatusBar } from "@ionic-native/status-bar/ngx";

import { AppComponent } from "./app.component";
import { AppRoutingModule } from "./app-routing.module";
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TimeAgo } from './util/time-ago';
import { NotificationsComponent } from './components/notifications/notifications.component';
import { AuthGuardService } from './service/autentificacao/auth-guard-service.service';
import { CurrencyPipe } from '@angular/common';
import { AutentificacaoService } from './service/autentificacao/autentificacao.service';
import { FileOpener } from "@ionic-native/file-opener/ngx";
import { File } from '@ionic-native/file/ngx';
import { SignaturePadModule } from 'angular2-signaturepad';
import { PdfViewerModule, PdfViewerComponent } from 'ng2-pdf-viewer';
import { IonicColorPickerModule } from 'ionic-color-picker';

@NgModule({
  declarations: [AppComponent,
    NotificationsComponent
  ],
  entryComponents: [NotificationsComponent],
  imports: [
    BrowserModule,
    IonicSelectableModule,
    IonicColorPickerModule,
    IonicModule.forRoot(),
    AppRoutingModule,
    HttpClientModule,
    SignaturePadModule,
    IonicStorageModule.forRoot({
      name: 'SigObra',
      driverOrder: ['indexeddb', 'sqlite', 'websql']
    })
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy },
    TimeAgo,
    Camera,
    AuthGuardService,
    AutentificacaoService,
    CurrencyPipe,
    FileOpener,
    File,
    PdfViewerComponent

  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
